const translate = document.querySelectorAll(".translate");
const big_title = document.querySelector(".big_title");
const header = document.querySelector("header");
const content = document.querySelector(".content");
const section = document.querySelector("section");
const image_container = document.querySelector(".imgContainer");
const opacity = document.querySelectorAll(".opacity");
const border = document.querySelector(".border");

let header_height = header.offsetHeight;
let section_height = section.offsetHeight;

window.addEventListener("scroll", () => {
  let scroll = window.pageYOffset;
  let sectionY = section.getBoundingClientRect();

  translate.forEach((element) => {
    let speed = element.dataset.speed;
    element.style.transform = `translateY(${scroll * speed}px)`;
  });

  opacity.forEach((element) => {
    element.style.opacity = scroll / (sectionY.top + section_height) + 0.1;
  });
  big_title.style.opacity = -scroll / (header_height / 2) + 1;

  content.style.transform = `translateY(${
    (scroll / (section_height + sectionY.top)) * 50 - 50
  }px)`;
  image_container.style.transform = `translateY(${
    (scroll / (section_height + sectionY.top)) * -100 + 100
  }px)`;
  border.style.width = `${(scroll / (sectionY.top + section_height)) * 30}%`;
});

function openForm() {
  document.getElementById("myForm").style.display = "block";
}

function closeForm() {
  document.getElementById("myForm").style.display = "none";
}

function openFormSign() {
  document.getElementById("myFormSignIn").style.display = "block";
}

function closeFormSign() {
  document.getElementById("myFormSignIn").style.display = "none";
}

function openFormReg() {
  document.getElementById("register").style.display = "block";
}

function closeFormReg() {
  document.getElementById("register").style.display = "none";
}
